import React, {useState} from 'react';

const Contact = ()=> {
  const [data, setData] =useState({
    fullname:"",
    phone:"",
    email:"", 
    message:"",
  });
  const InputEvent = (event) => {
    const {name , value } = event.target;
    setData((preVal) => {
     return {
      ...preVal,
      [name]: value,
     };
    });
  };

  const formSubmit = (e) => {
    e.preventDefault();
    alert(
      `Name is ${data.fullname}. Phone: ${data.phone}. Email: ${data.email}. Message: ${data.message}`
    );
  };
  return (
      <>
      <div className="my-5">
        <h1 className="text-center">Contact Us</h1>
      </div>
      <div className=" container-contact-div">
<div className="row">
  <div className="col-md-5 col-10 mx-auto">
    <form onSubmit={formSubmit}>
    <div className="my-3 pb-4 mb-2">
  <label for="exampleFormControlInput1" className="form-label">Full Name</label>
  <input type="name" className="form-control" id="exampleFormControlInput1" name="fullname" value={data.fullname} onChange={InputEvent} placeholder="Enter your name"/>
</div>
<div className="mb-3">
  <label for="exampleFormControlInput1" className="form-label">Phone</label>
  <input type="number" className="form-control" id="exampleFormControlInput1" name="phone" value={data.phone} onChange={InputEvent} placeholder="mobile Number"/>
</div>
<div className="mb-3">
  <label for="exampleFormControlInput1" className="form-label">Email address</label>
  <input type="email" className="form-control" id="exampleFormControlInput1" name="email" value={data.email} onChange={InputEvent} placeholder="name@example.com"/>
</div>
<div className="mb-3">
  <label for="exampleFormControlTextarea1" className="form-label">Enter Message</label>
  <textarea className="form-control" id="exampleFormControlTextarea1" rows="3" name="message" value={data.message} onChange={InputEvent}></textarea>
</div>
<div className="col-12 mb-4">
    <button className="btn btn-outline-primary" type="submit">Submit form</button>
  </div>
    </form>
  </div>
</div>
      </div>
  </>
  );
};
export default Contact;